import {connect} from "react-redux";
import React, {Component} from "react";
import OraModal from "eshop/modules/core/components/ui/Modal";
import {OraButton} from "eshop/components/OraCommonComponents";
import {getCimConsistentCheckoutErrors, getCustomerData} from "../../selectors";
import {getCartCode} from "eshop/modules/cart/selectors";
import {dismissBackendValidationErrors, gotoCartSummary} from "../../actions/app";


class CimConsistentErrorModalView extends Component {

    onClose(){
        this.props.dismiss();
    }

    renderInfo() {
        return (<span>{this.props.errors && this.props.errors.length > 0 ? this.props.textInfo.replace("$1",this.props.errors[0].peselFromCheckout).replace("$2",this.props.errors[0].peselFromSession):""}</span>);
    }

    render() {
        return (
            <OraModal showClose={true}  open={this.props.errors && this.props.errors.length > 0}  onClose={this.onClose.bind(this)} size="medium" id={'cim-consistent-error-modal'}>
                <div className="u-padding-all-m u-small-padding-s">
                    <div className="l-row u-spacing-top-m">
                        <div className="l-col l-col-12">
                            {this.renderInfo()}
                        </div>
                    </div>
                </div>
            </OraModal>);
    }
}


let mapStateToProps = state => ({
    errors: getCimConsistentCheckoutErrors(state)
});
let mapDispatchToProps = dispatch => ({
    dismiss: () => dispatch(dismissBackendValidationErrors()),
    action: () => dispatch(gotoCartSummary())
});

const CimConsistentErrorModal = connect(mapStateToProps, mapDispatchToProps)(CimConsistentErrorModalView);
export default CimConsistentErrorModal;
